.header-color {
  background: #0b5e86;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to bottom, #348AC7, #0b5e86);  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to bottom, #348AC7, #0b5e86); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.landing-grid {
  background: #0b5e86;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to bottom, #348AC7, #0b5e86);  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to bottom, #348AC7, #0b5e86); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  text-align: center;
  height: 100%;
  justify-content: center;
}

.my-img {
  height: 200px;
  scroll-padding-top: 5em;
  border-radius: 50%;
}

.banner-text {
  background-color: black;
  opacity: 0.8;
  width: 75%;
  margin: auto;
  border-radius: 10px; 
}

.banner-text h1 {
  font-size: 35px;
  font-weight: bold;
  color: white;
}

.banner-text hr {
  border-top: 3px solid white;
  width: 50%;
  margin: auto;
}

.banner-text p {
  color: white;
  font-size: 20px;
  padding: 1em; 
}

.social-links {
  display: flex;
  justify-content: space-between;
  width: 25%;
  margin: auto;
}

.social-links i {
  color: white;
  font-size: 5em;
  padding: 8px;
}



/* projects page CSS */
.projects-grid {
  display: flex;
}

/* contact page CSS */
.contact-body {
  margin: auto;
  width: 100%;
  height: 100%;
  justify-content: center;
  padding-top: 2em;
}

.contact-grid {
  text-align: center;
  justify-content: center;
  width: 80%;
  height: 550px;
  background: white;
  color: black;
  margin-bottom: 20px;
}

.contact-grid h2 {
  font-family: 'Anton', sans-serif;
}

.contact-grid p {
  font-family: 'Oxygen', sans-serif;
  font-weight: bold;
}

.contact-grid hr {
  border-top: 5px solid black;
  width: 50%;
  margin: auto;
}

.contact-list i {
  font-size: 45px;
  padding-right: 2rem;
  padding-left: 3rem;
  text-align: justify;
  text-justify: inter-word;
}

/*resume page CSS*/
.resume-right-col {
  background: #27221f;
  color: white;
}

/*aboutme page CSS*/
.about-me-col {
  padding-top: 50px;
  background: #27221f;
  color: white;
}

.about-me-col hr {
  border-top: 5px solid white;
  width: 30%;
  margin: auto;
}